<template>
  <div class="main" :style="mainStyle" @scroll="scrollChange">
    <div class="message_container" v-for="item in messageList" :key="item.id">
      <div class="from" v-show="item.openid != openid">
        <div class="avatar">
          <img :src="item.headImg" />
        </div>
        <div class="meText">
          {{ item.message }}
        </div>
      </div>
      <div class="To" v-show="item.openid == openid">
        <div class="meText">
          {{ item.message }}
        </div>
        <div class="avatar">
          <img :src="item.headImg" />
        </div>
      </div>
    </div>
    <div class="bottom_text">
      <div class="left_input">
        <input
          class="mesInput"
          @keydown="keyup_submit(event)"
          v-model="message"
          type="text"
          placeholder="请输入消息"
        />
      </div>
      <div class="right_icons">
        <img src="./../assets/images/sendVideo.png" class="btn" alt="" srcset="" />
        <img
          src="./../assets/images/sendtext.png"
          @click="sendMessage"
          class="btn"
          alt=""
          srcset=""
        />
      </div>
    </div>
  </div>
</template>

<script>
import { nextTick, reactive, toRefs } from "vue";
import request from "./../api/request.js";
import { getScreeInfo } from "./../util/util.js";
import { useRoute } from "vue-router";
import { getCode } from "./../api/wechat.js";
export default {
  name: "MessageOnWall",
  components: {},
  setup(props, context) {
    let route = useRoute();
    let open = getCode();
    let state = reactive({
      messageList: [{}, {}],
      openid: sessionStorage.getItem("openid"),
      pageNum: 1,
      signId: sessionStorage.getItem("userInfo")
        ? sessionStorage.getItem("userInfo").signId
        : "",
      message: "",
      hasMore: true,
      userInfo: JSON.parse(
        sessionStorage.getItem("userInfo") ? sessionStorage.getItem("userInfo") : "{}"
      ),
      mainStyle: {},
    });
    async function getMessageList(refresh) {
      // 历史记录
      return new Promise(async (resolve, reject) => {
        if (refresh) {
          state.hasMore = true;
          state.pageNum = 1;
          // let messageList = await request.get(
          //   "/api/v1/user/barrage/listUserBarrageData",
          //   {
          //     activeId: route.query.activityId,
          //     messageType: 1,
          //     pageNum: state.pageNum,
          //     pageSize: 10,
          //   }
          // );
          let messageList = await request.get(
            "/api/v1/user/barrage/showCurTimeUserBarrageData",
            {
              activityId: route.query.activityId,
              num: 10,
            }
          );
          // messageList.data.list.reverse();
          messageList.data;
          state.messageList = messageList.data;
          resolve(state.messageList);
        } else {
          if (state.hasMore) {
            // let messageList = await request.get(
            //   "/api/v1/user/barrage/showCurTimeUserBarrageData",
            //   {
            //     activityId: 1,
            //     num: 10,
            //   }
            // );
            // if (messageList.data.length > 0) {
            //   state.messageList = [
            //     ...messageList.data,
            //     ...state.messageList,
            //   ];
            // } else {
            //   state.hasMore = false;
            // }
          } else {
            console.log("已加载全部");
          }
        }
      });
    }
    async function getCurrentMessage() {
      let message = await request.get("/api/v1/user/barrage/showCurTimeUserBarrageData", {
        activeId: sessionStorage.getItem("activeId"),
      });
      let data = message.data;
      return data;
    }
    async function scrollChange(event) {
      console.log("scrollChange", event);
      let scrollTop = event.target.scrollTop;
      let isLoading = false;
      if (scrollTop == 0) {
        if (isLoading) {
        } else {
          isLoading = true;
          state.pageNum = state.pageNum + 1;
          let messageList = await getMessageList();
          nextTick(() => {
            isLoading = false;
          });
        }
      }
    }
    async function init() {
      let sysInfo = await getScreeInfo();
      console.log("pingmu", sysInfo);
      state.mainStyle = {
        // height: `${sysInfo.height}px`,
        height: `100%`,
        overflow: scroll,
      };
      getMessageList(true);
    }
    init();

    return {
      getMessageList,
      getCurrentMessage,
      scrollChange,
      ...toRefs(state),
    };
  },
  mounted() {
    console.log("mounted");
  },
  methods: {
    keyup_submit: function () {
      var evt = window.event || e;
      if (evt.keyCode == 13) {
        //回车事件
        this.sendMessage();
      }
    },
    sendMessage: async function () {
      console.log(this.message);
      let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
      if (this.message) {
        let resp = await request.post("/api/v1/user/barrage/saveUserBarrageData", {
          activityId: this.$route.query.activityId,
          message: this.message,
          messageType: 1,
          signId: userInfo ? userInfo.signId : this.userInfo ? this.userInfo.signId : "",
        });
        this.message = "";
        let messageList = this.messageList;

        this.getMessageList(true);
      } else {
      }
    },
  },
};
</script>
<style lang="scss" scoped>
html,
body,
#app,
.outer.main {
  height: auto;
}
.main {
  padding-bottom: 200px;
  overflow: scroll;
}

.from,
.To {
  display: flex;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 35px;
  padding-right: 35px;
  .avatar {
    width: 84px;
    height: 84px;
    border-radius: 50%;
    background: blue;
  }
  .meText {
    background: #ffffff;
    box-shadow: 0px 6px 14px 3px rgba(61, 123, 252, 0.21);
    border-radius: 44px;
    padding-top: 32px;
    padding-left: 28px;
    padding-bottom: 28px;
    padding-right: 28px;
    width: 481px;
  }
}
.from {
  .avatar {
    margin-right: 20px;
  }
}
.To {
  display: flex;
  justify-content: flex-end;
  .avatar {
    margin-left: 20px;
  }
}
.bottom_text {
  height: 102px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  background: #ffffff;
  width: 100%;
  .right_icons {
    width: 185px;
  }
  .left_input {
    width: 600px;
    .mesInput {
      height: 104px;
      width: 480px;
      margin-left: 35px;
    }
  }

  .right_icons {
    display: flex;
    .btn {
      width: 66px;
      height: 66px;
      border-radius: 50%;
    }
    .btn:first-child {
      margin-right: 20px;
    }
  }
}
</style>
